/**
 * 
 * @method addPrefixToPath
 * @param  {array}        prefixes 
 * @param  {string}       path    
 */
export function addPrefixToPath (prefixes, path) {
  //  `/` prefix
  const prefix = path.charAt(0) === '/' ? prefixes[0] : prefixes.join('/')
  return `/${prefix}/${path}`.replace(/\/+$/, '').replace(/\/\/+/g, '/') || '/'
}



